'use strict';

angular.module('videoHub')
  .controller('CarouselDetailController', function ($scope, $q, $routeParams, AlertService,
      CarouselService, DetailPageControllerConstants, Restangular) {

    var normalizeSlideIndicies = function () {
      // update indicies when sortable is updated
      _.each($scope.carousel.slides, function (slide, i) {
        slide.position = i;
      });
    };

    $scope.carousel = {};
    $scope.$getCarousel = function (carouselId) {
      return CarouselService.one(carouselId).get()
        .then(function(data) {
          $scope.carousel = data;
          normalizeSlideIndicies();
        })
        .catch(function(data) {
          console.log(data);
        });
    };

    // ui-sortable setup
    $scope.sortableOptions = {
      placeholder: 'dropzone',
      stop: function () {
        normalizeSlideIndicies();
      }
    };

    /**
     * Add a new slide.
     */
    $scope.addSlide = function () {
      $scope.carousel.slides.push({});
      normalizeSlideIndicies();
    };

    /**
     * Remove a slide.
     *
     * @param {Object} slide - slide to remove from carousel.
     */
    $scope.removeSlide = function (slide) {
      if (slide) {
        var i = _.findIndex($scope.carousel.slides, {id: slide.id});
        $scope.carousel.slides.splice(i, 1);
        normalizeSlideIndicies();
      }
    };

    /**
     * Save changes to carousel.
     *
     * @returns {Promise} resolves based on {@link AlertService}.
     */
    $scope.$saveCarousel = function () {
      var promise;

      if ($scope.carouselDetailForm.$valid) {
        // form is valid, attempt to save
        promise = $scope.carousel.save()
          .then(function () {
            // success in saving, display success alert
            return AlertService.success(
                DetailPageControllerConstants.SAVED_ALERT_MESSAGE,
                true);
          })
          .catch(function () {
            return AlertService.error(
              DetailPageControllerConstants.SOMETHING_WENT_WRONG_ALERT_MESSAGE,
              true);
          });
      } else {
        // form is not valid, reject saving with an alert
        promise = AlertService.error(
            DetailPageControllerConstants.PLEASE_FILL_IN_ALL_REQUIRED_FIELDS_ALERT_MESSAGE,
            true);
      }

      return promise;
    };

    $scope.$deleteCarousel = function () {
      return AlertService.info('If you would like to delete a slider, please contact your friendly sysadmin.', true);
    };

    // set up
    if (_.has($routeParams, 'carouselId')) {
      // exising carousel, use route id
      $scope.$getCarousel($routeParams.carouselId);
    } else {
      // this is a new carousel
      Restangular.restangularizeElement(null, $scope.carousel, 'carousels');
    }
  });
